/* CSS for chat interface */
.chat-container {
  height: calc(100% - 70px);
  overflow-y: scroll;
}

.chat-container .message {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
}

.chat-container .message .message-body {
  padding: 10px;
  border-radius: 10px;
  max-width: 70%;
  align-self: flex-end;
  background-color: #dcf8c6;
}

.chat-container .message .message-body .time {
  font-size: 12px;
  margin-top: 5px;
  color: gray;
  text-align: right;
}

.chat-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
}

.chat-input input {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 20px;
  margin-right: 10px;
}

.chat-input button {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  background-color: #1c87c9;
  color: white;
  font-weight: bold;
  cursor: pointer;
}
/* CSS for avatar */
.avatar {
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #ddd;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/withfaithcowork.appspot.com/o/withfaith%20cowork%20logo%20.png?alt=media&token=891272b0-778f-4e75-9309-e940d522f10c");
  background-size: cover;
  background-position: center;
  cursor: pointer;
}

/* CSS for menu */
.menu {
  display: none;
  position: absolute;
  top: 60px;
  float: right;
  right: 0;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.menu li {
  padding: 10px;
