/*** Main chat UI ***/

/********************/

#header,
#form {
  display: grid;
  grid-template-columns: 50px auto 50px;
  align-items: center;
}

#header {
  padding: 6px;
}

/* Conversation */

#message-board {
  display: grid;
  align-self: end;
  grid-auto-rows: minmax(min-content, max-content);
  padding: 5px 0;
  box-sizing: border-box;
  max-height: 115%;
  overflow-x: hidden;
  overflow-y: scroll;
}

.post {
  position: relative;
  margin: 4px 10px;
  padding: 10px;
  border-radius: 10px;
  font-size: 0.9rem;
  word-break: break-word;
}

.post-user {
  justify-self: left;
  margin-right: 50px;
  background: rgba(255, 152, 0, 0.1);
  /*border-top-left-radius: 0;*/
}

.post-bot {
  justify-self: right;
  margin-left: 50px;
  background: rgba(64, 83, 191, 0.1);
  /*border-top-right-radius: 0;*/
}

.post:after {
  content: "";
  position: absolute;
  right: -10px;
  top: 10px;
  width: 0;
  height: 0;
  border: 10px solid transparent;
  border-top: 0;
}

.post-user:after {
  left: -10px;
  border-right-color: rgba(255, 152, 0, 0.1);
  border-left: 0;
}

.post-bot:after {
  right: -10px;
  border-left-color: rgba(64, 83, 191, 0.1);
  border-right: 0;
}

.timestamp {
  font-size: 0.6em;
  opacity: 0.4;
  margin: 5px 0 -5px;
  display: block;
}

/* Chat Form */

#form {
  padding: 12px;
  /*min-height: 40px;*/
  grid-template-rows: auto 1fr;
}

#emoijis {
  grid-column: 1 / -1;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  padding: 5px;
  grid-gap: 5px;
  margin-bottom: 5px;
}

#message {
  padding: 5px;
  font-size: 0.9rem;
  min-height: 25px;
  max-height: 25vh;
  overflow: auto;
  background: rgba(0, 0, 0, 0.05);
  /*
  font-style: italic; 
  */
  outline: none;
  resize: none;
}

#form.focus {
  background: rgba(255, 152, 0, 0.1);
}

#message.focus {
  background: rgba(255, 255, 255, 0.5);
}

/* Custom Scrollbar */

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: hsl(231, 50%, 50%, 10%);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #b30000;
}

.text-light {
  color: #f0f0f0;
}

.align-center {
  text-align: center;
}

